mos alterar este Termo periodicamente. Nesse caso, Se forem feitas alteraes no Termo, iremos notific-lo, colocando um aviso destacado no Site e/ou envio de um e-mail para o seu endereo de e-mail. Qualquer alterao neste Termo ser imediatamente efetivada para os novos usurios do Site, caso contrrio, essas mudanas sero efetivadas aps trinta dias do envio de uma notificao por e-mail para o usurio ou trinta dias aps a publicao de um aviso no Site. O usurio  responsvel, em todos os momentos pela atualizao de sua conta, bem como do endereo de e-mail.\n              "),_c('br'),_vm._v("3. Suas Informaes e Conta Para determinados usos do Site, o Programa Agro+ pode exigir que o usurio fornea informaes pessoais (tais como identificao ou informaes de contato) ou para criar uma conta de usurio. O usurio concorda que qualquer informao que fornecer em conexo com a sua conta ser sempre legtima, correta, completa e atualizada. O usurio ser responsvel por todas as atividades que decorram sob sua conta e, portanto, o usurio concorda em manter sua senha confidencial. O usurio concorda em nos notificar imediatamente sobre qualquer uso no autorizado de sua conta, ou se o usurio acredita que sua senha no  mais confidencial. Reservamo-nos o direito de exigir que o usurio altere seu nome de usurio e/ou senha, se acreditarmos que sua conta no  mais segura. O usurio no deve:(a) fornecer informaes falsas como parte de suas informaes de conta, ou em conexo com o Site, (b) criar uma conta para ningum alm de si mesmo, (c) criar ou usar mais de uma conta a qualquer momento (d) transferir sua conta para terceiros, (e) permitir que outros usem sua conta, ou (f) usar ou acessar contas de outras pessoas.\n              "),_c('br'),_vm._v("4. Privacidade O usurio concorda e autoriza a utilizao dos seus dados pessoais, em conformidade com a Poltica de privacidade do Programa Agro+. Consulte a Poltica de privacidade da Programa Agro+.\n              "),_c('br'),_vm._v("5. Conduta do Site Ao utilizar o Site, o usurio concorda em cumprir com as seguintes diretrizes: No  permitido postar ou transmitir informao, dado, texto, software, grficos, sons, fotografias, vdeos, mensagens ou outro contedo que seja ilegal, ofensivo, impreciso, difamatrio, obsceno, fraudulento, prejudicial, ameaador ou abusivo. No interferir no uso de outros usurios do Site. No postar ou fazer upload de qualquer vrus, worms, arquivo corrompido ou outro software capaz de perturbar, incapacitar ou prejudicar o funcionamento do Site. Ir cumprir com este Termo e quaisquer leis ou regulamentos aplicveis (incluindo quaisquer leis relativas  exportao de dados ou software para os Estados Unidos ou outros pases relevantes). No  permitido usar o Site para divulgar ou vender produtos ou servios para os outros ou se envolver em atividades como concursos, sorteios ou esquemas de pirmide. No se passar por qualquer pessoa ou entidade, declarar falsamente ou deturpar sua afiliao com uma pessoa ou entidade. No enviar ou transmitir contedo que o usurio no tem o direito de publicar ou transmitir sob qualquer lei ou sob relaes contratuais ou fiducirias (tais como informao privilegiada, informaes confidenciais, etc). No usar o Site para solicitar, obter ou armazenar dados pessoais ou senhas de outros usurios.\n              "),_c('br'),_vm._v("6. Violao do Termo Se o usurio violar qualquer item do Termo ou informaes de outros usurios postadas no Site, ou se o Programa Agro+ acreditar que sua conduta ou contedo  ofensivo ou ilegal, que viola os direitos ou ameaa prejudicar terceiros, e que podem criar responsabilidade para o Programa Agro+ ou terceiros, reservamo-nos o direito de investigar e tomar as medidas legais cabveis, incluindo a remoo de contedo do Site, notificao das autoridades competentes sobre a fonte do contedo, impedindo-o de acessar o Site e promover o encerramento de sua Conta.\n              "),_c('br'),_vm._v("7. Envio de Contedo O usurio concede ao Programa Agro+, que  isento de royalties, perptuo, irrevogvel, intransfervel, a licena para utilizar, reproduzir, modificar, adaptar, publicar, traduzir, preparar trabalhos derivados, distribuir, executar e exibir todo e qualquer contedo (no todo ou em parte) que o usurio postar ou enviar para o Site e de incorporar o contedo do usurio em outras obras, de qualquer forma, mdia ou tecnologia conhecida ou posteriormente desenvolvida. O usurio declara e garante que: (a) o usurio tem o direito de conceder a licena anterior e postar e enviar o Contedo do Usurio, e (b) o Contedo do Usurio no pode infringir, negar ou violar direitos de terceiros (incluindo direitos de propriedade). O usurio  o nico responsvel por seu Contedo do Usurio. Reserva-se o Programa Agro+, o direito a seu exclusivo critrio para excluir ou deixar inalterado qualquer Contedo submetido no Site.\n              "),_c('br'),_vm._v("8. Links de Terceiros no Site O Programa Agro+ ou terceiros podem fornecer links do Site para outros Sites ou servios; no tem controle sobre sites e Servios de Terceiros; no endossa e no  responsvel por quaisquer sites e Servios de Terceiros ou pela informao, material, produtos ou servios contidos ou acessveis atravs dos sites e Servios de Terceiros. O usurio reconhece que o Programa Agro+ no  responsvel, direta ou indiretamente, por qualquer perda ou dano causado ou alegadamente causados por ou em conexo com o uso ou dependncia de qualquer informao, material, produtos ou servios. Todos os sites e servios de terceiros tm seus prprios termos de uso e polticas de privacidade diferentes das nossas. Os termos do Programa Agro+ e Poltica de Privacidade apenas se aplica exclusivamente a este e no se aplicam a todos os sites e Servios de Terceiros. Podemos permitir que terceiros forneam certas informaes apresentadas no Site. Qualquer informao de terceiros no  patrocinada, apoiada, recomendada, ou licenciada pelo Programa Agro+. O usurio deve contatar diretamente a terceiros e/ou suas referncias para as informaes mais precisas. Sua interao correspondente ou transaes com terceiros atravs do Site, incluindo a participao em promoes ou de e-commerce e quaisquer termos, condies, garantias ou representaes associadas com interaes de Terceiros, so exclusivamente entre o usurio e tais terceiros. O usurio concorda que o Programa Agro+ no ser responsvel por qualquer perda ou dano de qualquer tipo incorridos em conexo com suas interaes de Terceiros ou a presena de tais terceiros no site. O Programa Agro+ reserva o direito (mas no  obrigado), de monitorar ou tomar qualquer ao que considerar til relativamente, nas disputas entre o usurio e terceiros usando o Site. Na medida do permitido pelas leis aplicveis, o usurio decide liberar o Programa Agro+ de toda e qualquer responsabilidade ou reclamao relacionados a: (a) qualquer contedo postado no Site pelo usurio ou por terceiros, ou (b) pela conduta, seja online ou off-line de terceiros.\n              "),_c('br'),_vm._v("9. Indenizao O usurio concorda em indenizar, defender e isentar o Programa Agro+, suas afiliadas, conselho de administrao, diretores, agentes, empregados, parceiros e fornecedores de qualquer responsabilidade, perda, reclamao, demanda, custos e despesas (incluindo honorrios razoveis de advogados) relacionados com : O uso do Site. Qualquer violao ou inexatido de qualquer representao ou garantia feita ao abrigo destes Termos. Violao sua destes Termos. Violao de sua legislao aplicvel ou quaisquer direitos de terceiros, ou Sua postagem de contedo do usurio ao Site.\n              "),_c('br'),_vm._v("10. Uso e Armazenamento de Contedo do Site O Programa Agro+ pode estabelecer prticas gerais e limites relativos  utilizao do Site, incluindo, sem limitao, o nmero mximo de dias que o contedo estar disponvel ou mantido no Site. O Programa Agro+, a seu exclusivo critrio, reserva-se o direito de modificar estas prticas gerais e limites a qualquer tempo.\n              "),_c('br'),_vm._v("11. Posse dos Direitos de Contedo e Propriedade Intelectual O Programa Agro+ e seus licenciadores so proprietrios do Site e de todo contedo nele inserido e de todos os direitos de propriedade intelectual relacionados com o que precede. O usurio no pode sob qualquer hiptese copiar, distribuir, vender, alugar, executar, sublicenciar, modificar ou preparar trabalhos derivados do Site ou o Contedo do Site, no todo ou em parte, exceto quando expressamente autorizado. Qualquer uso do Site no expressamente permitido por estes Termos ser considerado violao destes Termos e violao dos direitos autorais, marcas e outras leis. O Programa Agro+ ir considerar qualquer comentrio ou sugesto que o usurio fornecer como no-confidencial e no-proprietria. O usurio concede  Agro+ o direito de usar qualquer comentrio ou sugesto que fornecer de qualquer maneira e para qualquer finalidade, sem qualquer obrigao de compensar.\n              "),_c('br'),_vm._v("12. Iseno de Garantia Nada nestes termos excluir ou limitar a garantia do programa Agro+ para medidas que no podem ser legalmente excludas ou limitadas por lei. Algumas jurisdies no permitem a excluso de garantias ou condies. Portanto, apenas excluses e limitaes que so legais na jurisdio se aplicaro ao usurio e a nossa responsabilidade  limitada aos termos permitidos por lei. O usurio concorda expressamente que o uso do site est por sua conta e que o site, contedo do Site e todos os servios so fornecidos da forma que est e como est disponvel. O programa Agro+ renuncia expressamente todas as garantias e condies de qualquer tipo, expressa ou implcita, mas sem limitao. As garantias e condies de comercializao, adequao para um determinado fim, ttulo e no violao. Em particular, nem o programa Agro+, nem qualquer de suas controladas ou coligadas representam ou garantem que: o uso do Site ou contedo do Site vai atender suas necessidades; o uso do Site ou contedo do Site ser ininterrupto, oportuno, seguro e livre de erros, livre de vrus ou outro cdigo nocivo, qualquer informao obtida pelo usurio como resultado do seu uso do Site ser preciso ou de confiana, ou que os defeitos no funcionamento ou funcionalidade de qualquer software fornecido ao usurio como parte dos servios sero corrigidos. O usurio faz download ou obtm material do Site por sua prpria conta e risco, o usurio ser o nico responsvel por quaisquer danos ao seu computador ou outro dispositivo ou perda de dados que resultam em danos materiais. No conselho ou informao, verbal ou por escrito, obtido do programa Agro+ atravs do site, ou contedo do Site, qualquer garantia no expressa nestes termos.\n              "),_c('br'),_vm._v("13. Limitaes de Responsabilidade Nada nestes termos excluir ou limitar a responsabilidade do Programa Agro+ por perda ou responsabilidade, medida em que tal no pode ser legalmente excludo ou limitado. Algumas jurisdies no permitem a excluso de responsabilidade por perda ou danos causados por violao, negligncia do contrato, violao dos termos implcitos ou danos. Assim, s as limitaes e excluses que so legais na jurisdio se aplicaro ao usurio e a nossa responsabilidade limitada ao limite estabelecido por lei. O usurio concorda expressamente que o programa Agro+, suas filiadas, conselho de administrao, diretores, agente, empregados, parceiros e fornecedores no sero responsveis por qualquer um dos seguintes: (A) Qualquer dano indireto, incidental, especial, consequentes ou exemplares na conexo com o site, contedo do site, quaisquer servios prestados na conexo com o Site, ou estes termos, causados e sob qualquer teoria de responsabilidade. Isso deve incluir, mas no ser limitada a perda de lucros (incorrida direta ou indiretamente) perda de reputao, boa vontade ou negcios, perda de dados, custo de aquisio de bens ou servios, ou outras perdas intangveis; e (B) Qualquer perda ou dano que pode incorrer, incluindo, mas no limitado, a perda ou danos em conexo com: Confiana a qualquer contedo colocado pelo usurio na integridade, preciso ou existncia de qualquer informao publicada no Site. Qualquer alterao que o Programa Agro+ possa fazer no Site, ou qualquer seo permanente ou temporria do fornecimento do Site (ou quaisquer funcionalidades dentro do Site). Supresso, corrupo ou falha no armazenamento de qualquer contedo ou outros dados de comunicaes mantidos ou transmitidos pela ou atravs da utilizao do Site. Falta de prestao de suas informaes pelo Programa Agro+ sobre informaes de conta; sua falta para manter sua senha ou requisitos de conta segura e confidencial; ou Qualquer relao ou transao realizada com terceiros. As limitaes de responsabilidade do Programa Agro+ para o usurio nos pargrafos acima aplica-se ou no, desde que o Programa Agro+ tenha sido comunicado ou ter conhecimento da possibilidade de tais perdas.\n              "),_c('br'),_vm._v("14. Poltica de Violao de Direitos Autorais O Programa Agro+ leva reivindicaes de violao de direitos autorais a srio. Ns vamos responder a acusaes de violao de direitos autorais, conforme a lei aplicvel e manter uma poltica de encerrar as contas de infratores reincidentes. Se o usurio acredita que quaisquer materiais acessveis a partir do Site viola seus direitos autorais, o usurio pode solicitar a remoo desses materiais (ou acesso aos mesmos) a partir do Site, enviando notificao por escrito ao nosso endereo de email. O aviso prvio dever incluir substancialmente o seguinte: Sua assinatura fsica ou eletrnica. Identificao de direitos autorais da obra que o usurio acredita ter sido violada ou se o pedido envolve vrias obras no Site, uma lista representativa de tais trabalhos; Identificao do material que o usurio acredita estar violando de forma suficientemente precisa para nos permitir localizar o material. Informao adequada pela qual podemos entrar em contato (incluindo o seu nome, endereo, nmero de telefone e, se disponvel, endereo de e-mail). Uma declarao de que o usurio tem uma boa f, que o uso do material protegido por direitos autorais no  autorizado pelo proprietrio dos direitos autorais, seu agente ou a lei. Uma declarao de que as informaes na notificao por escrito so precisas. Uma declarao, sob pena de perjrio, que o usurio est autorizado a agir em nome do proprietrio dos direitos autorais. O endereo da ATN segue abaixo: ATN  Associao Telecentro de Informao e Negcios SHCGN - CR 706/707, BLOCO C, SALAS 204, 205 E 206 - ASA NORTE BRASLIA-DF CEP 70.740-790 suporte @atn.org.br\n              "),_c('br'),_vm._v("15. Marcas O Programa Agro+ e seu logotipo so marcas registradas. O usurio no deve usar qualquer marca do Programa Agro+, sem a permisso prvia e por escrito. Todas as marcas registradas de terceiros no Site so de propriedade de seus respectivos proprietrios.\n              "),_c('br'),_vm._v("16. Modificaes para o Site O Programa Agro+ reserva o direito de modificar o Site, suspender o acesso a todos ou a partes do Site, ou fechar o Site por tempo indeterminado, com ou sem aviso prvio. O usurio concorda que o Programa Agro+ no ser responsabilizado pelo usurio ou terceiros por qualquer modificao ou suspenso do Site, no todo ou em parte.\n              "),_c('br'),_vm._v("17. Terminao Sujeito a esta Seo, o presente acordo permanecer em pleno vigor e efeito, enquanto o usurio usar o Site ou manter uma conta. O usurio pode encerrar sua conta a qualquer momento e por qualquer motivo atravs da pgina apropriada de gerenciamento de conta no Site. O Programa Agro+ tem o direito de encerrar sua conta, mediante o envio de aviso prvio, no endereo de e-mail que o usurio fornecer em sua conta. O usurio concorda que ns no teremos nenhuma responsabilidade por quaisquer custos, despesas, perdas, danos, ou responsabilidades decorrentes ou relacionadas ao encerramento da sua conta, o seu acesso e uso do Site, ou este Contrato. Mesmo aps este Acordo for denunciado por qualquer razo, as seguintes disposies do presente acordo permanecer em vigor: Sees 7, 8, 9, 12 a 14, 17, 19 e 20. Sem limitar o precedente, se o usurio usar, copiar, distribuir, vender, exibir, executar, sublicenciar ou preparar trabalhos derivados de qualquer parte do Site, em violao destes Termos, o seu direito de usar o Site cessar imediatamente e o usurio deve, em exclusivo critrio do Programa Agro+, devolver ou destruir quaisquer cpias de materiais do Site que o usurio fez.\n              "),_c('br'),_vm._v("18. Divulgao A ATN est localizada no endereo abaixo: ATN  Associao Telecentro de Informao e Negcios SHCGN - CR 706/707, BLOCO C, SALAS 204, 205 E 206 - ASA NORTE - BRASLIA-DF CEP 70.740-790 Telefone: 55 (61) 3965-3399 O usurio pode relatar queixas ao Instituto de Defesa do Consumidor  PROCON, no endereo: SCS Venncio 2000 - bl. B-60, sala 240 - Braslia - DF - CEP: 70.333-900 Telefone: 151 | E-mail procon@procon.df.gov.br\n              "),_c('br'),_vm._v("19. Comunicaes Eletrnicas Para fins contratuais, o usurio concorda em receber comunicaes do Programa Agro+ em um formulrio eletrnico e (b) concorda que todos os termos e condies, acordos, notificaes, divulgaes e outras comunicaes que o Programa Agro+ fornece ao usurio eletronicamente, satisfazem quaisquer requisitos legais e que tais comunicaes satisfariam como se fosse em via impressa. Todas as notificaes enviadas ao usurio pelo Programa Agro+ podem ser feitas por qualquer e-mail.\n              "),_c('br'),_vm._v("20. Disposies Gerais Estes Termos e quaisquer Termos Adicionais constituem, completa e ficam includas no acordo final entre o usurio e o Programa Agro+ sobre o Site e substituem todos os acordos anteriores ou contemporneos, entendimentos e comunicaes, escritas ou orais, entre o usurio e o Programa Agro+ (incluindo, mas no se limitando a, qualquer verso anterior destes Termos). Estes Termos e a resoluo de qualquer controvrsia ou reivindicao decorrente ou relacionadas com estes Termos ou do Site sero regidos e interpretados de acordo com as leis do Pas, sem dar efeito a quaisquer princpios de conflitos de lei. A falha do Programa Agro+ em insistir em aplicao rigorosa de qualquer disposio destes Termos no deve ser interpretada como uma renncia de qualquer proviso ou direito. Todas as isenes s sero eficazes por escrito. Se qualquer disposio destes Termos for considerada invlida ou inexequvel, as demais disposies destas Condies sero aplicadas de acordo com seus termos. O usurio reconhece que o Site e Contedo do Site contm segredos comerciais valiosos e informaes de propriedade do Programa Agro+, que qualquer violao ou ameaa da seo intitulada Propriedade do Contedo e dos Direitos de Propriedade Intelectual constituir um dano imediato, irreparveis ao Programa Agro+ para o qual os danos monetrios seriam um remdio inadequado, e que a medida cautelar  uma soluo adequada para tal violao. Os ttulos das sees destes Termos so apenas para convenincia e no tm efeito legal ou contratual. Inclui e incluindo, no so limitativos. Independentemente de qualquer estatuto ou lei em contrrio, o usurio renuncia a seu direito de apresentar qualquer reclamao contra o Programa Agro+ mais de 1 ano aps tal reclamao surgir pela primeira vez.\n            ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"color":"indigo","text":""},on:{"click":_vm.negarTermos}},[_vm._v("Negar")]),_c('div',{staticClass:"flex-grow-1"}),_c('v-btn',{attrs:{"color":"green darken-1","text":""},on:{"click":function($event){_vm.modal = false}}},[_vm._v("Aceito")])],1)],1)],1)],1)],1)],1)],1)}
